.serviceDiscoveryApp{width:100%;height:100%;overflow:auto;padding:15px}.serviceDiscoveryApp .header{margin:0 0 10px}.serviceDiscoveryApp .collapsibleHeader{font-weight:bold;margin-bottom:10px;cursor:pointer}.serviceDiscoveryApp .collapsibleSection{margin:10px 0;max-width:100%;overflow:auto}.serviceDiscoveryApp .target{width:100%;grid-template-columns:80px 1fr;color:#fff;padding:10px;border-radius:10px;column-gap:10px;row-gap:5px;height:fit-content;padding:15px}.serviceDiscoveryApp .target .tableCell{border:1px solid rgba(255,255,255,.3);padding:5px;text-align:left;line-height:1.25}.serviceDiscoveryApp .target th.tableCell{vertical-align:bottom;background-color:rgba(255,255,255,.05);padding-top:15px;padding-bottom:15px}.serviceDiscoveryApp .target td.tableCell{vertical-align:top}.serviceDiscoveryApp tr:nth-child(2n) td{background-color:rgba(255,255,255,.05)}.serviceDiscoveryApp .buttonGroup{margin:10px 0 20px}.serviceDiscoveryApp .buttonGroup button{border:none;border-right:1px solid #004a99}.serviceDiscoveryApp .buttonGroup button:last-child{border-right:none}.serviceDiscoveryApp .badge{font-size:12px;display:inline-block;font-weight:bold;color:#fff;padding:1px 5px;line-height:1.5;border-radius:5px;margin-right:3px;white-space:nowrap;user-select:all}.serviceDiscoveryApp .badge.info{background:#007bff}.serviceDiscoveryApp .badge.healthy{background:#2ecc40}.serviceDiscoveryApp .badge.error{background:#dc3545}.serviceDiscoveryApp .link{color:#007bff}