.buttonContainer{width:100%;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.buttonContainer>:not(:last-child){margin-bottom:15px}.buttonGithub{background-color:var(--ps-c-gray-light-3)}.buttonGithub:hover{background-color:var(--ps-c-gray-light-1)}.buttonGitlab{background-color:var(--ps-c-orange-light-2)}.buttonGitlab svg{width:1em;vertical-align:-0.125em}.buttonGitlab:hover{background-color:var(--ps-c-orange-dark-2)}.buttonGoogle{background-color:var(--ps-c-orange-light-1)}.buttonGoogle svg{width:1em;vertical-align:-0.125em}.buttonGoogle:hover{background-color:var(--ps-c-orange-dark-1)}