.dropdownContainer{position:relative;display:inline-block}.dropdownMenuItem{display:block;padding:4px 16px;clear:both;font-weight:normal;font-size:11px;line-height:20px;color:var(--ps-c-gray);text-decoration:none;white-space:nowrap;cursor:pointer;width:100%;text-align:left;border:0}.dropdownMenuItem:hover{background-color:var(--ps-c-gray-light-4)}.menuShow{display:flex;min-width:150px;position:absolute;right:0;z-index:1000;flex-direction:column;border:1px solid var(--ps-c-gray-light-3);background-color:var(--ps-c-white);box-shadow:0 5px 15px var(--ps-c-black);color:var(--ps-c-black)}.menuHide{display:none}