@import'@szhsin/react-menu/dist/index.css';.dropdownMenu{box-shadow:0 2px 5px rgba(0,0,0,.33);padding:.25rem 0;background-color:#272727 !important;color:#fff !important}.dropdownMenu ul[class*=rc-menu]{background-color:#272727;color:#fff !important}.dropdownMenu li[class*=hover],.dropdownMenu div[class*=hover]{background-color:#3b3b3b}.dropdownMenu [class*=rc-menu--open]:empty{display:none}.dropdownMenu [class*="rc-menu__item active"]{background-color:#3b3b3b;color:#df8b53}.dropdownMenu [class*=rc-menu__item--focusable]{background-color:inherit !important;padding-top:1rem;padding-bottom:1rem}.dropdownMenu [class*=rc-menu__item--focusable] input{width:100%}.dropdownMenuButton{outline:none;display:inline-block;position:relative;border-radius:4px;background-color:#272727;border:1px solid #4d4d4d;margin-right:5px;padding-right:20px}.dropdownMenuButton::after{content:"▾";position:absolute;top:3px;right:5px}.dropdownMenuButton:hover{cursor:pointer}