.button{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;cursor:pointer;border:1px solid #4d4d4d;border-radius:4px}.iconWithText{margin-right:5px}.default{background:#272727}.default:hover:not(:disabled){background-color:#414141}.default:disabled{color:rgba(255,255,255,.3);pointer-events:none}.primary{color:#222;background-color:#2ecc40;border-left-color:#2ecc40;border-right-color:#2ecc40;border-top-color:#2ecc40;border-bottom-color:#2ecc40}.primary:hover{background-color:#54d964}.primary:disabled{border-color:#7ee289;background-color:#7ee289;pointer-events:none}.secondary{background-color:#0074d9;border-left-color:#0074d9;border-right-color:#0074d9;border-top-color:#0074d9;border-bottom-color:#0074d9}.secondary:hover{background-color:#0d8eff}.secondary:disabled{border-color:#40a6ff;background-color:#40a6ff;pointer-events:none}.danger{color:#fff;background-color:#dc3545;border-left-color:#dc3545;border-right-color:#dc3545;border-top-color:#dc3545;border-bottom-color:#dc3545}.danger:hover{background-color:#e4606d}.danger:disabled{border-color:#eb8c95;background-color:#eb8c95;pointer-events:none}.grouped{margin:0;border-right:0;border-radius:0;padding-left:12px;padding-right:12px}.grouped:first-child{border-left:1px solid #4d4d4d;border-radius:4px 0 0 4px}.grouped:last-child{border-right:1px solid #4d4d4d;border-radius:0 4px 4px 0}